import React from "react";
import { useAuth } from "../../context/auth.js";
import { Navigate } from "react-router-dom";

const UserRoute = ({ children }) => {
  const [auth, setAuth] = useAuth();
  if (localStorage.getItem("token")) {
    return <>{children}</>;
  } else {
    return <Navigate to={"/"} />;
  }
};

export default UserRoute;
