import React, { useEffect, useState } from "react";
import Layout from "./../page-layout/Layout";
import "../../styles/Admin.css";
import { message } from "antd";
import axios from "axios";
import DashboardLayout from "./DashboardLayout";
import { NavLink, useNavigate } from "react-router-dom";
const CreateCategory = () => {
  const [name, setName] = useState();
  const [categories, setCategories] = useState([]);
  const navigate = useNavigate();
  const handleSubmit = async () => {
    try {
      //   const { category } = req.body;
      //   alert(name);
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/category/createcategory`,
        { name },
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success(res?.data?.message);
        window.location.reload();
        // navigate("/create-category");

      }
    } catch (error) {
      message.error("error in here")
      // console.log(error);
    }
  };
  const updateCategory = async (c) => {
    try {
      const res = await axios.put(
        `${process.env.REACT_APP_API}/api/v1/category/updatecategory/${c}`,
        { name },
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success(res?.data?.message);
        window.location.reload();
      }
    } catch (error) {
      console.log(error);
    }
  };
  const handledelete = async (c) => {
    try {
      window.prompt("are you sure");
      const res = await axios.delete(
        `${process.env.REACT_APP_API}/api/v1/category/deletecategory/${c}`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success(res?.data?.message);
        window.location.reload();
      }
    } catch (error) {
      console.log(error);
    }
  };
  const getAllCategory = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/category/getcategories`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success(res?.data?.message);
        setCategories(res?.data?.data);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllCategory();
  }, []);
  return (
    <Layout>
      <div className="cat-main">
        <div className="c-main-right text-center">
          <h1 className="text-center">Categories</h1>

          {categories ? (
            <>
              <table className="c-table">
                <thead>
                  <tr>
                    <td>category name</td>
                    <td>delete</td>
                    <td>update</td>
                  </tr>
                </thead>
                <tbody>
                  {categories.map((c) => (
                    <>
                      <tr>
                        <td>{c.name}</td>
                        <td>
                          <button
                            className=" btn btn-danger"
                            onClick={() => handledelete(c.slug)}
                          >
                            delete
                          </button>
                        </td>
                        <td>
                          <button
                            className="  btn btn-primary"
                            onClick={() => updateCategory(c.slug)}
                          >
                            update
                          </button>
                        </td>
                      </tr>
                    </>
                  ))}
                </tbody>
              </table>
            </>
          ) : (
            "null"
          )}
        </div>
        <div className="c-main-left ">
          <div className="c-main-left-bars mb-3">
            <NavLink
              className={"u-left-buttons"}
              to={"/create-category"}
              style={{ textDecoration: "none" }}
            >
              Create-Category
            </NavLink>{" "}
            <NavLink
              className={"u-left-buttons"}
              to={"/create-product"}
              style={{ textDecoration: "none" }}
            >
              Create-product
            </NavLink>
            <NavLink
              className={"u-left-buttons"}
              to={"/all-users"}
              style={{ textDecoration: "none" }}
            >
              All-Users
            </NavLink>{" "}
          </div>
          <h1 className="text-center mb-3">Create category here</h1>
          <div className="">
            <label htmlFor="">categoryName =</label>
            <input
              type="text"
              onChange={(e) => {
                setName(e.target.value);
              }}
            />
          </div>
          <div className="create">
            <button
              className="btn btn-primary mt-3 mb-2 "
              onClick={handleSubmit}
            >
              Create
            </button>
            <h5>here will create new category</h5>
          </div>
        </div>
      </div>
    </Layout>
  );
};
export default CreateCategory;
