import React from "react";
import { useAuth } from "../../context/auth.js";
import { Navigate } from "react-router-dom";

const AdminRoute = ({ children }) => {
  const [auth, setAuth] = useAuth();

  if (auth?.user?.role === true) {
    return <>{children}</>;
  } else {
    return <Navigate to={"/"} />;
  }
};

export default AdminRoute;
