import axios from "axios";
import Layout from "./../page-layout/Layout";
import { useEffect, useState } from "react";
import { message } from "antd";
import { useParams } from "react-router-dom";

const SingleProduct = () => {
  const [singleProduct, setSingleProduct] = useState();
  const params = useParams();
  const getSingleProduct = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/getsingleproduct/${params.slug}`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success("successfull get product");
        setSingleProduct(res?.data?.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getSingleProduct();
  }, []);
  return (
    <Layout>
      <div className="single-p">
        <div className="single-product">
          <img
            src={`${process.env.REACT_APP_API}/api/v1/product/product-photo/${singleProduct?._id}`}
            alt="none"
          />
          <div className="bottom-product">
           {singleProduct? (<>
            <table className="text-center">
              <tbody>
                <tr>
                  <td>Name </td>
                  <td>{singleProduct?.name}</td>
                </tr>{" "}
                <tr>
                  <td>Price </td>
                  <td>
                    {singleProduct?.price?.toLocaleString("INDIAN", {
                      style: "currency",
                      currency: "INR",
                    })}
                  </td>
                </tr>{" "}
                <tr>
                  <td>Quantity </td>
                  <td>{singleProduct?.quantity}</td>
                </tr>
              </tbody>
            </table></>):("nothing is here")}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default SingleProduct;
