import React, { useEffect, useState } from "react";
import Layout from "../page-layout/Layout";

import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import { NavLink, useParams } from "react-router-dom";
import { Checkbox, Radio, message } from "antd";
import { Prices, bannerData } from "../constant/data.js";
import axios from "axios";
import { useCart } from "../../context/cart";

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 1,
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 1,
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
  },
};

const Home = () => {
  const [categories, setCategories] = useState();
  const [checked, setChecked] = useState([]);
  const [products, setProducts] = useState([]);
  const [radio, setRadio] = useState([]);
  const [cart, setCart] = useCart();
  const [page, setPage] = useState(1);
  const [filterData, setFilterData] = useState([]);
  const params = useParams();

  const getAllProducts = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/product-list/${page}`
      );
      if (res.data.success) {
        message.success("done");
        setProducts(res?.data?.products);
      }
      //   alert("njk");
    } catch (error) {
      alert(error);
      console.log(error);
    }
  };
  useEffect(() => {
    // if (params?.slug) {
    getAllProducts();
    // }
  }, []);

  const getAllCategory = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/category/getcategories`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success(res?.data?.message);
        setCategories(res?.data?.data);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllCategory();
  }, []);

  const handleFilter = async (value, id) => {
    try {
      // alert(id);
      let all = [...checked];
      if (value) {
        all.push(id);
      } else {
        all = all.filter((a) => a !== id);
      }
      setChecked(all);
      // alert(checked)
      // alert(id)}catch(error){
    } catch (error) {}
  };

  const getFilter = async () => {
    try {
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/product/productfilter`,
        { radio, checked }
      );
      if (res.data.data) {
        setProducts(res?.data?.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (checked.length || radio.length) {
      getFilter();
    }
  }, [checked, radio]);
  useEffect(() => {
    if (!checked.length && !radio.length) {
      getAllProducts();
      // loadMore();
    }
  }, [checked, radio]);

  const totalProduct = async () => {
    try {
      // alert("hii")
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/getproductcount`
      );
      // alert(data?.data);
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    totalProduct();
  }, []);

  //pagination
  const loadMore = async () => {
    try {
      // alert("hii");
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/product-list/${page}`
      );
      setProducts([...products, ...data?.products]);
      // alert(JSON.stringify());
      // alert();
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (page === 1) return;
    loadMore();
  }, [page]);
  return (
    <Layout>
      <div className="">
        {" "}
        <Carousel
          responsive={responsive}
          showDots={true}
          infinite={true}
          autoPlay={true}
          autoPlaySpeed={3000}
        >
          {bannerData?.map((b) => (
            <div className="w-banner">
              <img src={b.url} alt="" />
            </div>
          ))}
        </Carousel>
        {/* {checked} */}
        <div className="second-section mt-4 ">
          <div className="left-sec">
            <div className="h-filters">
              <div className="t-filter ">
                <h4 className="text-center">Filter</h4>
                {categories?.map((c) => (
                  <Checkbox
                    style={{ color: "black" }}
                    key={c._id}
                    onChange={(e) => handleFilter(e.target.checked, c._id)}
                  >
                    {c.name}
                  </Checkbox>
                ))}
              </div>
              <div className="b-filter ">
                <h4 className="text-center">Prices Filter</h4>
                <Radio.Group onChange={(e) => setRadio(e.target.value)}>
                  {Prices?.map((p) => (
                    <div className="">
                      <Radio
                        style={{ color: "black" }}
                        key={p._id}
                        value={p.array}
                      >
                        {p.name}
                      </Radio>
                    </div>
                  ))}
                </Radio.Group>
              </div>
            </div>
          </div>
          <div className="right-sec">
            <h4 className="text-center">Products Are</h4>

            <div className="home-products mt-0">
              {products?.map((p) => (
                <div className="home-card ">
                  <img
                    src={`${process.env.REACT_APP_API}/api/v1/product/product-photo/${p._id}`}
                    alt="photo"
                  />
                  <div className="home-card-body">
                    <h4>Name: {p.name}</h4>
                    <h6>Descri: {p.description}</h6>
                    <h6>Price: {p.price}</h6>
                    <h6 className="button">
                      <button
                        className="btn btn-primary m-1"
                        onClick={() => {
                          setCart([...cart, p]);
                          localStorage.setItem(
                            "cart",
                            JSON.stringify([...cart, p])
                          );
                        }}
                      >
                        Add
                      </button>
                      <NavLink
                        to={`/category/${p.slug}`}
                        style={{
                          listStyle: "none",
                          textDecoration: "none",
                          color: "black",
                          margin: "12px auto",
                        }}
                      >
                        {" "}
                        <button className=" btn btn-info text-center">more</button>
                      </NavLink>
                    </h6>
                  </div>
                </div>
              ))}
            </div>
            <button
              className="btn btn-primary m-2"
              onClick={(e) => {
                e.preventDefault();
                setPage(page + 1);
                loadMore();
              }}
            >
              loadMore {page}
            </button>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Home;
