import React, { useEffect, useState } from "react";
import Layout from "./../page-layout/Layout";
import { useCart } from "../../context/cart";
// import { deleteCategoryController } from './../../../../server/controllers/categoryControllers';
import DropIn from "braintree-web-drop-in-react";
import axios from "axios";
import { useAuth } from "../../context/auth";
import { useNavigate } from "react-router-dom";
const CartPage = () => {
  const [cart, setCart] = useCart();
  const [auth, setAuth] = useAuth();
  const [clientToken, setClientToken] = useState("");
  const [instance, setInstance] = useState();
const navigate=useNavigate()
  const handleDelete = async (value) => {
    try {
      //   alert(value);
      const myCart = [...cart];
      myCart.findIndex((item) => item._id === value._id);
      myCart.splice("index", 1);
      localStorage.setItem("cart", JSON.stringify(myCart));
      setCart(myCart);
    } catch (error) {
      console.log(error);
    }
  };
  //get payment gateway token
  const getToken = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/braintree/token`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      setClientToken(data?.clientToken);
    } catch (error) {
      console.log(error);
    }
  };
  const handlePayment = async () => {
    try {
      const {nonce}=await instance.requestPaymentMethod()
      const {data}=await axios.post(`${process.env.REACT_APP_API}/api/v1/product/braintree/payment`,{nonce,cart},{
        headers:{
          authorization:localStorage.getItem("token")
        }
      })
      localStorage.removeItem("cart");
      setCart([])
      navigate("/")
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getToken();
  }, [auth?.token]);
  return (
    <Layout>
      <div className="cart-page">
        <h1 className="text-center">Cart-Page</h1>

        <div className="cart-main">
          <div className="cart-list">
            {cart?.map((c) => (
              <div className="cart-card">
                <>
                  <img
                    height={"150px"}
                    width={"150px"}
                    src={`${process.env.REACT_APP_API}/api/v1/product/product-photo/${c._id}`}
                    alt="photo"
                  />
                  <div className="cart-card-body">
                    <h4>Name:{c.name}</h4>
                    <h4>Price:{c.price}</h4>
                    <h4>Description:{c.description}</h4>
                    <h2>
                      <button
                        className="btn btn-danger"
                        onClick={() => handleDelete(c)}
                      >
                        Delete
                      </button>
                    </h2>
                  </div>
                </>
              </div>
            ))}
          </div>

          <div className="cart-order">
            <div className="mt-2 cart-order-top">
              {!clientToken || !auth?.token || !cart?.length ? (
                "hello"
              ) : (
                <>
                  <DropIn
                    options={{
                      authorization: clientToken,
                      paypal: {
                        flow: "vault",
                      },
                    }}
                    onInstance={(instance) => setInstance(instance)}
                  />

                  <button
                    className="btn btn-primary"
                    onClick={handlePayment}
                    disabled={ !instance || !auth?.user?.name}
                  >
                    make payment{" "}
                  </button>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CartPage;
