import React from "react";
import { Routes, Route } from "react-router-dom";
import Home from "./components/public-pages/Home";
import About from "./components/public-pages/About";
import LoginPage from "./components/authentication/LoginPage";
import Registration from "./components/authentication/Registration";
import Contact from "./components/public-pages/Contact";
import UserDashboard from "./components/user-pages/UserDashboard";
import AdminDashboardPage from "./components/admin-pages/AdminDashboardPage";
import CreateCategory from "./components/admin-pages/CreateCategory";
import CreateProduct from "./components/admin-pages/CreateProduct";
import PageNotFound from "./components/PageNotFound";
import AllProducts from "./components/admin-pages/AllProducts";
import AllUsers from "./components/admin-pages/AllUsers";
import SingleProduct from "./components/public-pages/SingleProduct";
import UpdateProfile from "./components/user-pages/UpdateProfile";
import ProductsPage from "./components/admin-pages/ProductsPage";
import CartPage from "./components/public-pages/CartPage";
import AdminRoute from "./components/routes/AdminRoute";
import UserRoute from "./components/routes/UserRoute";
import PbRoute from "./components/routes/PbRoute";

const App = () => {
  return (
    <>
      <Routes>
        <Route path={"/"} element={<Home />} />
        <Route path={"/about"} element={<About />} />
        <Route
          path={"/login"}
          element={
            <PbRoute>
              {" "}
              <LoginPage />{" "}
            </PbRoute>
          }
        />
        <Route
          path={"/registration"}
          element={
            <PbRoute>
              {" "}
              <Registration />{" "}
            </PbRoute>
          }
        />
        <Route path={"/contact"} element={<Contact />} />
        <Route path={"/cart-page"} element={<CartPage />} />
        <Route
          path={"/dashboard/false"}
          element={
            <UserRoute>
              {" "}
              <UserDashboard />{" "}
            </UserRoute>
          }
        />
        <Route
          path={"/update-profile"}
          element={
            <UserRoute>
              {" "}
              <UpdateProfile />{" "}
            </UserRoute>
          }
        />

        <Route
          path={"/dashboard/true"}
          element={
            <AdminRoute>
              <AdminDashboardPage />
            </AdminRoute>
          }
        />
        <Route
          path={"/create-category"}
          element={
            <AdminRoute>
              {" "}
              <CreateCategory />{" "}
            </AdminRoute>
          }
        />
        <Route
          path={"/create-product"}
          element={
            <AdminRoute>
              {" "}
              <CreateProduct />{" "}
            </AdminRoute>
          }
        />
        <Route
          path={"/all-users"}
          element={
            <AdminRoute>
              <AllUsers />{" "}
            </AdminRoute>
          }
        />

        <Route path={"/all-products"} element={<AllProducts />} />
        <Route path={"/product/:slug"} element={<ProductsPage />} />

        <Route path={"/category/:slug"} element={<SingleProduct />} />
        <Route path={"*"} element={<PageNotFound />} />
      </Routes>
    </>
  );
};

export default App;
