// import { Layout } from "antd";
import React, { useState } from "react";
import { useAuth } from "../../context/auth";
import { LayoutContext } from "antd/es/layout/layout";
import Layout from "../page-layout/Layout";
import axios from "axios";
import { message } from "antd";
import { useNavigate } from "react-router-dom";

const UpdateProfile = () => {
  const [auth, setAuth] = useAuth();
  const [name, setName] = useState();
  const [surname, setSurname] = useState();
  const [address, setAddress] = useState();
  const [answer, setAnswer] = useState();
  const [password, setPassword] = useState();
  const navigate = useNavigate();
  const handleSubmit = async () => {
    try {
      const res = await axios.put(
        `${process.env.REACT_APP_API}/api/v1/user/updateuser/${auth?.user?._id}`,
        {
          name,
          answer,
          password,
          surname,
        },
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res?.data?.success) {
        // alert(res?.data?.message);
        setAuth({
          ...auth,
          user: res?.data?.data,
        });
        let ls = localStorage.getItem("auth");
        ls = JSON.parse(ls);
        ls.user = res?.data?.data;
        localStorage.setItem("auth", JSON.stringify(ls));
        message.success("successfull");
        navigate(`/dashboard/${auth?.user?.role}`);
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <Layout>
      <div className="update-profile">
        <div className="mb-4 mt-3 ">
          <label htmlFor="name">Name</label>
          <input
            type="text"
            onChange={(e) => {
              setName(e.target.value);
            }}
            placeholder={auth?.user?.name}
          />
        </div>{" "}
        <div className="mb-4 mt-3">
          <label htmlFor="name">Answer</label>
          <input
            type="text"
            onChange={(e) => {
              setAnswer(e.target.value);
            }}
            placeholder={auth?.user?.answer}
          />
        </div>{" "}
        <div className="mb-4 mt-3">
          <label htmlFor="name">Email</label>
          <input type="email" disabled placeholder={auth?.user?.email} />
        </div>{" "}
        <div className="mb-4 mt-3">
          <label htmlFor="name">password</label>
          <input
            type="password"
            onChange={(e) => {
              setPassword(e.target.value);
            }}
          />
        </div>{" "}
        <div className="mb-4 mt-3">
          <label htmlFor="name">surname</label>
          <input
            type="text"
            onChange={(e) => {
              setSurname(e.target.value);
            }}
            placeholder={auth?.user?.surname}
          />
        </div>{" "}
        <button className="btn btn-primary" onClick={handleSubmit}>
          update
        </button>
      </div>
    </Layout>
  );
};

export default UpdateProfile;
